import { $axios } from "./app-http";

export async function getBloodTestResults(patientId) {
    const {
        data: { btdisplay }
    } = await $axios.get("blood-test/display-blood-tests-list", {
        params: { patientId }
    });
    return btdisplay;
}

export async function getBloodGroupTests() {
    const {
        data: { bloodGroups }
    } = await $axios.get("blood-test/get-blood-groups");
    return bloodGroups;
}

export async function addBloodTestOrderItems(payload) {
    const { data } = await $axios.post(
        "blood-test/add-blood-test-order-items",
        payload
    );
    return data;
}
